.row{
    margin-top: 50px;
    margin-left:20px;
    color:#fff;

}

.posters{
    display:flex;
    padding: 20px;
    overflow-x:scroll;
    overflow-y: hidden;

}

.posters::-webkit-scrollbar{
    display:none;
}




.poster{
    max-height:250px;
    margin-left:10px;
    cursor: pointer;
    

}

.subtitle{
    color: darkgrey;
    text-align: center;
    text-shadow: gray;
    
}

.subtitle:hover{
  transform: scaleX(1.1);
}

.smallPoster{
    max-height: 150px;
    margin-left:10px;
    cursor: pointer;

}

.poster:hover{
    transform: scale(1.1);
}
.smallPoster:hover{
    transform: scale(1.1);
}


