.banner{
   
    background-size: cover;
    height: 448px;
    color: white;
}

.content{
padding-top:140px;
height: 190px;

}

.toppicks{
 color:white;
 text-shadow: dimgrey;
 margin-left: 1rem;
}

.toppicks:hover{
    text-decoration-line: underline;
}

.title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
    margin-left: 1rem;
}

.description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 0.9rem;
    height: 80px;
    margin-left: 1rem;
    max-width: 450px;

}



.button{
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51,51, 52, 0.5);
    cursor: pointer;
    margin: right 1rem;
    margin-left: 1rem;

}

.button:hover{
    color: black;
    background-color:#e6e6e6;
}


.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
}

.youtubeframe{
    margin-bottom: 0%;

}




